import React from 'react'
import {
  Form,
  FormGroup,
  Label,
  Input,
  Col,
  Button
} from 'reactstrap'
import Map from './GoogleMap'

export default function Contact(props) {
    const flexContainer = {'display': 'flex', 'justifyContent': 'center'}
    const input = {'height': '8vh', 'borderColor': '#2c3e50'}
    const submit= {'backgroundColor': '#2ecc71', 'color': 'white', 'height': '20%', 'width': '20vw', 'fontSize': '100%', 'margin': '1%'}
    return (
        <div id='contact'>
            {props.navbar}
            <h1 style={flexContainer}>Contact Me</h1>
            <div style={flexContainer}>
                <Col xs={3} />
                <Col xs={6}>
                    <Form id='contactform' action='https://formspree.io/suzanne@medicareandmore.info' method="POST">
                        <FormGroup>
                            <Label for='name' sm={1}>Name</Label>
                            <Input style={input} type='text' name='name' id='name' required />
                        </FormGroup>
                        <FormGroup>
                            <Label for='phone' sm={1}>Phone</Label>
                            <Input style={input} type='tel' name='phone' id='phone' required />
                        </FormGroup>
                        <FormGroup>
                            <Label for='email' sm={1}>Email</Label>
                            <Input style={input} type='email' name='email' id='email' required />
                        </FormGroup>
                        <FormGroup>
                            <Label for='message' sm={1}>Message</Label>
                            <Input style={{'height': '24vh', 'borderColor': '#2c3e50'}} type='textarea' name='message' id='message' required />
                        </FormGroup>
                        <Input type="hidden" name="_subject" value="New Message for Medicare & More" />
                        <Input type="text" name="_gotcha" style={{'display': 'none'}} />
                        <Button type='submit' value='Send' style={submit}>Send</Button>
                    </Form>
                </Col>
                <Col xs={3} />
            </div>

            <Map />
        </div>
    )
}